.reviews-section {
  padding-top: 50px;
  padding-bottom: 50px;
  text-align: center;

  @media screen and (min-width: 1366px) {
    padding-top: 110px;
    padding-bottom: 110px;
  }
}

.reviews-container {
  padding-left: 20px;
  padding-right: 20px;

  @media screen and (min-width: 768px) {
    position: relative;
    padding-left: 40px;
    padding-right: 40px;
  }

  @media screen and (min-width: 1366px) {
    padding-left: 25px;
    padding-right: 25px;
  }
}

.reviews-title {
  margin-bottom: 20px;
  font-weight: 800;
  font-size: 20px;
  line-height: 1.3;
  letter-spacing: 0.03em;

  color: var(--dark-text-bg-color);

  @media screen and (min-width: 768px) {
    margin-bottom: 30px;
    font-size: 26px;
  }

  @media screen and (min-width: 1366px) {
    margin-bottom: 50px;
    font-size: 46px;
  }
}

.reviews-article {
  background-color: var(--white-text-bg-color);
  box-shadow: -5px 8px 26px rgba(0, 0, 0, 0.04);
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 20px;
  padding-bottom: 60px;
  margin-bottom: 20px;

  @media screen and (min-width: 768px) {
    display: flex;
    padding-left: 40px;
    padding-right: 40px;
    padding-top: 40px;
    margin-bottom: 30px;
  }

  @media screen and (min-width: 1366px) {
    max-width: 1090px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 82px;
    padding-right: 112px;
    padding-top: 50px;
    padding-bottom: 50px;
    margin-bottom: 40px;
  }
}

.reviews-article__img-wrapper {
  @media screen and (min-width: 768px) {
    min-width: 150px;
  }
}

.reviews-article__text-wrapper {
  @media screen and (min-width: 768px) {
    text-align: left;
    margin-left: 46px;
  }
}

.reviews-article__img {
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;
}

.reviews-article__icon {
  margin-bottom: 20px;
}

.reviews-article__title {
  margin-bottom: 10px;
  font-weight: 800;
  font-size: 14px;
  line-height: 1.2;

  color: var(--accent-color);

  @media screen and (min-width: 768px) {
    margin-bottom: 20px;
    font-size: 20px;
  }

  @media screen and (min-width: 1366px) {
    margin-bottom: 10px;
    font-size: 30px;
  }
}

.reviews-article__description {
  margin-bottom: 25px;
  line-height: 1.25;

  color: var(--grey-text-bg-color);

  @media screen and (min-width: 768px) {
    margin-bottom: 20px;
  }

  @media screen and (min-width: 1366px) {
    font-size: 14px;
    line-height: 1.21;
  }
}

.reviews-article__text {
  line-height: 1.67;

  color: #3d3d3d;

  @media screen and (min-width: 1366px) {
    font-size: 18px;
    line-height: 1.56;
  }
}

.reviews-article__link {
  text-decoration: underline;
  color: var(--accent-color);
}

.slider {
  display: flex;
  justify-content: space-between;

  @media screen and (min-width: 768px) {
    justify-content: center;
  }
}

.slider__button {
  padding: 0;
  border: transparent;
  background-color: transparent;
  cursor: pointer;
}

.slider__button--left {
  @media screen and (min-width: 768px) {
    position: absolute;
    top: 50%;
    left: 5%;
    transform: translate(-50%);
  }
}

.slider__button--right {
  @media screen and (min-width: 768px) {
    position: absolute;
    top: 50%;
    right: 2%;
    transform: translate(-50%);
  }
}

.slider__list {
  display: flex;
}

.slider__item:not(:last-child) {
  margin-right: 10px;

  @media screen and (min-width: 768px) {
    margin-right: 30px;
  }
}

.slider__item:not(:nth-child(-n + 3)) {
  @media screen and (max-width: 767px) {
    display: none;
  }
}

.slider__item:nth-child(3) {
  @media screen and (max-width: 767px) {
    margin-right: 0px;
  }
}
